.personal {
  margin: 20px;
  width: 40%;
  display: inline;
  background: #fff;
  border-radius: 5px;
  -webkit-box-shadow: 0px 1px 4px #aaaaaa;
          box-shadow: 0px 1px 4px #aaaaaa;
  height: calc(100vh - 100px);
  position: -webkit-sticky;
  position: sticky;
  top: 80px;
  overflow: auto;
  -webkit-animation: fadeIn 0.5s ease-in-out;
          animation: fadeIn 0.5s ease-in-out;
}

.personal h3 {
  margin: 20px auto;
  font-size: 30px;
  font-weight: 400;
  font-family: 'Poppins';
  color: #374146;
}

.empty-queue {
  width: 80%;
  font-size: 20px;
  margin: 50px auto;
  color: #545E63;
}

.personal:hover {
  -webkit-box-shadow: 0px 0px 4px 1px #16affb;
          box-shadow: 0px 0px 4px 1px #16affb;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Phones */

@media only screen and (max-width: 767px) {
  .personal {
    height: 100%;
    width: 100% !important;
    top: 5px !important;
  }
}



